.faq .title-container .title, .faq .title-container .desc, .faq .content .title, .faq .content .title-sec, .faq .content .title-desc {
  font-family: "Open Sans", sans-serif;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: -0.1px;
  text-align: center;
  color: white; }

.faq {
  position: relative;
  overflow: hidden; }
  .faq .title-container {
    max-width: 1000px;
    text-align: center;
    margin: auto;
    padding: 10px; }
    .faq .title-container .title {
      display: inline;
      font-size: 20px;
      font-weight: 700;
      color: #354F53 !important; }
      @media screen and (min-width: 1440px) {
        .faq .title-container .title {
          font-size: 34px; } }
    .faq .title-container .desc {
      text-align: center;
      font-size: 16px;
      font-weight: normal;
      color: #354F53 !important; }
      @media screen and (min-width: 1440px) {
        .faq .title-container .desc {
          font-size: 19.5; } }
  .faq .content .content-section {
    margin-bottom: 108px; }
    @media screen and (min-width: 1440px) {
      .faq .content .content-section {
        margin-top: 48px; } }
  .faq .content .content-row {
    justify-content: center;
    margin-bottom: 50px;
    padding: 20px; }
    @media screen and (min-width: 768px) {
      .faq .content .content-row {
        padding: 40px; } }
  .faq .content .title {
    font-size: 24px;
    font-weight: 800;
    color: #354F53; }
    @media screen and (min-width: 1440px) {
      .faq .content .title {
        font-size: 36px; } }
  .faq .content .title-sec {
    font-size: 24px;
    font-weight: 800;
    color: #354F53;
    padding: 0 10px;
    text-align: left !important; }
  .faq .content .title-desc {
    font-size: 14px;
    color: #46737A;
    width: 100vw;
    padding: 0 15px; }
    @media screen and (min-width: 1440px) {
      .faq .content .title-desc {
        font-size: 16px; } }
  .faq .content .faq-wrapper {
    padding: 20px 10px; }
    @media screen and (min-width: 1000px) {
      .faq .content .faq-wrapper {
        padding: 20px 10%; } }
    .faq .content .faq-wrapper .qna-container {
      cursor: pointer;
      padding: 10px; }
      .faq .content .faq-wrapper .qna-container .nav-item {
        display: flex; }
      .faq .content .faq-wrapper .qna-container .nav-link {
        flex: 1;
        display: inline-block;
        color: #354F53;
        background-color: rgba(255, 255, 255, 0);
        border-color: rgba(255, 255, 255, 0);
        padding-left: 0;
        padding-right: 0; }
        .faq .content .faq-wrapper .qna-container .nav-link:hover {
          color: #354F53; }
      .faq .content .faq-wrapper .qna-container .arrow {
        display: inline-block;
        float: right;
        width: 20px;
        transition: all 0.3s ease; }
      .faq .content .faq-wrapper .qna-container .rotate-arrow-up {
        transform: rotateX(0); }
      .faq .content .faq-wrapper .qna-container .rotate-arrow-down {
        transform: rotateX(180deg); }
  .faq .content .faq-inner .card {
    border-radius: 0;
    border-top: 2px solid #00D7AF;
    border-left: 0;
    border-right: 0;
    border-bottom: 0; }
    .faq .content .faq-inner .card .card-body {
      padding: 10px;
      font-size: 16px;
      color: #46737A;
      letter-spacing: -0.2px; }
      @media screen and (min-width: 820px) {
        .faq .content .faq-inner .card .card-body {
          padding: 1.25rem; } }
  .faq .content .faq-inner ol {
    padding-left: 20px; }
    .faq .content .faq-inner ol li {
      padding: 10px; }
